import React from 'react'

import { Link } from 'gatsby'
import TextLoop from 'react-text-loop';
import 'react-sticky-header/styles.css';
import StickyHeader from 'react-sticky-header';

import twitterIcon from '../images/twitter.png'
import headerStyle from './header.module.css'

const Header = (props) => {

    function ToTwitter() {
        window.open('https://twitter.com/akshanshd', '_blank')
    }

    return (
        <StickyHeader
        headerOnly
        header = {
            <header className={headerStyle.header}>

                <div className={headerStyle.row}>
                    <Link to='/' className={headerStyle.home}>
                        <h1>
                            akshansh jain
                            <sup>
                                <img onClick={() => ToTwitter()} alt={'Akshansh Jain Twitter'}
                                         className={headerStyle.twitterIcon + ' ' + headerStyle.hvrWobbleSkew} src={twitterIcon} />
                            </sup>
                        </h1>
                        <TextLoop interval={1500}>
                            <p className={headerStyle.loopText}>ANDROID DEVELOPER</p>
                            <p className={headerStyle.loopText}>WEBSITE DEVELOPER</p>
                            <p className={headerStyle.loopText}>DESIGN ENTHUSIAST</p>
                        </TextLoop>
                    </Link>
                </div>
                    
                <nav>
                    <ul>
                        <li>
                            <Link to='/work' className={headerStyle.navItem} activeClassName={headerStyle.active}>work</Link>
                        </li>

                        <li>
                            <Link to='/about' className={headerStyle.navItem} activeClassName={headerStyle.active}>about</Link>
                        </li>
                        
                        <li>
                            <Link to="/contact" className={headerStyle.navItem} activeClassName={headerStyle.active}>contact</Link>
                        </li>

                        <li>
                            <a href="https://medium.com/@jainakshansh" target="_blank" rel="noopener noreferrer" className={headerStyle.navItem}>blog</a>
                        </li>
                    </ul>
                </nav>
                
            </header>
        }/>
    )
}

export default Header